.system {
  background: linear-gradient(180deg, #D4DAE4 0%, rgba(212, 218, 228, 0.00) 100%);
  padding: 4rem 0;
  h2 {
    max-width: 50rem;
    text-align: center;
    color: #0072AF;
    margin: 0 auto;
  }
  &__wrap {
    position: relative;
    margin-top: 3.75rem;
  }
  &__items {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    gap: 2.5rem;
  }
  &__items-left,
  &__items-right {
    max-width: 28.125rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
  }
  &__item {
    display: flex;
    color: #4E5960;
    i {
      flex-shrink: 0;
      width: 2.25rem;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 1.25rem;
      line-height: 130%;
      margin-left: 1.975rem;
    }
  }
  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 35.625rem;
  }
  &__group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 6.25rem;
  }
  &__btn {
    flex-shrink: 0;
    width: 15.5rem;
    height: 3.75rem;
    margin-right: 2rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__info {
    max-width: 27.5rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
}
.result {
  padding: 4rem 0;
  h2 {
    max-width: 50rem;
    text-align: center;
    color: #0072AF;
    margin: 0 auto;
  }
  &__group {
    max-width: 62.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2.8125rem auto 0;
  }
  &__subtitle {
    text-align: center;
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #4E5960;
    text-transform: uppercase;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0 1.5rem;
    span {
      font-size: 1.125rem;
      line-height: 130%;
      color: #0072AF;
    }
    i {
      width: 1rem;
    }
  }
  &__slider-wrap {
    margin-top: 1rem;
  }
  .slider {
    position: relative;
    overflow: hidden;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      background: #ffffff;
    }
  }
  .before {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    z-index: 2;
    width: 50%;
    height: 100%;
  }
  .change {
    position: absolute;
    left: 48.3%;
    top: 0;
    z-index: 10;
    width: 0.5rem;
    height: 100%;
    background: #FFDD00;
    box-shadow: 4px 4px 8px 11px rgba(255, 221, 0, 0.2);
  }
  .change::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100%;
    width: 2.5rem;
    height: 2.5rem;
    background-color: #FFDD00;
    transform: translate(-50%, -50%);
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2.5rem;
  }
  &__dots-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin: 0 1rem;
    .slick-active {
      background: #1397DE;
      &:after {
        border-color: #1397DE;
      }
    }
    li {
      position: relative;
      width: 0.75rem;
      height: 0.75rem;
      border-radius: 50%;
      color: transparent;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        border: 0.0625rem solid #7A7B7A;
        border-radius: 50%;
      }
    }
  }
  &__prev {
    position: static;
    width: 2.0625rem;
    height: auto;
    box-shadow: unset;
  }
  &__next {
    position: static;
    width: 2.0625rem;
    height: auto;
    box-shadow: unset;
  }
}
.offer {
  z-index: 2;
  background-image: url("../img/offer-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 4rem 0 6.25rem;
  &__inner {
    max-width: 48.75rem;
    background: #FFF;
    border-radius: 1.5625rem;
    padding: 3.75rem;
    margin-left: auto;
  }
  h2 {
    position: relative;
    color: #0072AF;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    margin-top: 2.5rem;
    li {
      display: flex;
      align-items: center;
      color: #4E5960;
      i {
        flex-shrink: 0;
        width: 2.25rem;
        img {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 1.5rem;
        line-height: 130%;
        margin-left: 1.975rem;
      }
    }
  }
  &__btn {
    width: 15.5rem;
    height: 3.75rem;
    margin-top: 3.5rem;
    span {
      font-size: 1.125rem;
    }
  }
}
.advantages3 {
  padding: 5rem 0;
  .advantages__inner {
    max-width: 63.4375rem;
  }
  .advantage-card {
    text-align: start;
    &__icon {
      width: auto;
      max-width: 8.75rem;
      height: 6.75rem;
      margin: 0;
    }
    &__text {
      margin-top: 1.5rem;
      strong {
        color: #FD0;
      }
    }
  }
  .advantages__group {
    display: flex;
    align-items: center;
  }
  .advantages__btn {
    margin: 0;
  }
  .advantages__info {
    text-align: start;
    color: #ffffff;
    margin: 0 0 0 1.5rem;
  }
}
.application {
  padding: 6.25rem 0;
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #FFF;
    box-shadow: 0 1.875rem 5rem 0 rgba(98, 109, 117, 0.25);
    border-radius: 1.25rem;
    margin: 0 auto;
  }
  &__left {
    width: 42%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2.5rem;
  }
  &__right {
    width: 58%;
  }
  &__img {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 0 1.25rem 1.25rem 0;
      object-fit: cover;
    }
  }
  h2 {
    font-size: 1.5rem;
    color: #0072AF;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 2rem;
    margin-top: 2.5rem;
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      width: 2.5rem;
      margin-right: 1rem;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 21.25rem;
      font-size: 1.25rem;
      line-height: 130%;
      color: #4E5960;
    }
  }
  &__btn {
    max-width: 20.625rem;
    height: 3.75rem;
    margin-top: 2.5rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__info {
    max-width: 20.625rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1rem;
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__img3 {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
  }
}

@media screen and (max-width: 1700px) {
  .system {
    &__img {
      max-width: 25rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .system {
    &__img {
     display: none;
    }
    &__group {
      margin-top: 3.75rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .system {
   &__item {
     span {
       font-size: 1.25rem;
     }
   }
  }
}

@media screen and (max-width: 1023px) {
  .system {
    padding: 2rem 0;
    &__wrap {
      margin-top: 2rem;
    }
    &__item {
      i {
        width: 1.5rem;
      }
      span {
        font-size: 1.125rem;
        margin-left: 1rem;
      }
    }
    &__group {
      margin-top: 2.5rem;
    }
  }
  .result {
    padding: 2rem 0;
    &__group {
      margin-top: 2rem;
    }
    &__subtitle {
      font-size: 1.125rem;
    }
  }
  .offer {
    padding: 2rem 0;
    &__inner {
      padding: 2rem;
    }
    ul {
      margin-top: 2rem;
      li {
        i {
          width: 1.5rem;
        }
        span {
          font-size: 1.125rem;
          margin-left: 1rem;
        }
      }
    }
    &__btn {
      margin-top: 2rem;
    }
  }
  .advantages3 {
    padding: 4rem 0;
    .advantage-card {
      &__icon {
        max-width: 5rem;
        height: 5rem;
      }
      &__text {
        margin-top: 1rem;
      }
    }
  }
  .application {
    padding: 4rem 0;
    &__left {
      width: 45%;
      padding: 2rem;
    }
    &__right {
      width: 55%;
    }
    h2 {
      font-size: 1.25rem;
    }
    &__items {
      margin-top: 2rem;
    }
    &__item {
      &-icon {
        width: 2rem;
      }
      &-text {
        font-size: 1rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .application {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__img {
      img {
        border-radius: 0 0 1.25rem 1.25rem;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .system {
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.5rem;
    }
    &__group {
      display: block;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
  }
  .result {
    &__subtitle {
      font-size: 1rem;
    }
    &__info {
      span {
        font-size: 0.875rem;
      }
      i {

      }
    }
  }
  .advantages3 {
    .advantages__group {
      display: block;
    }
    .advantages__info {
      margin: 1rem 0 0 0;
    }
  }
}