.services-audit {
  background-image: url("../img/services-bg3.png");
  padding: 6.25rem 0;
  margin-top: 0;
  &__inner {
    max-width: 82.1875rem;
    margin: 0 auto;
  }
  h2 {
    max-width: 50rem;
    text-align: center;
    margin: 0 auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 6.25rem / 3));
    gap: 2.5rem 3.125rem;
    margin-top: 2.5rem;
  }
  .services-card {
    &__icon {
      img {
        filter: unset;
      }
    }
    &__title {
      color: #4E5960;
      margin-bottom: auto;
    }
  }
  &__group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3.75rem auto 0;
  }
  &__btn {
    flex-shrink: 0;
    width: 15.625rem;
    height: 3.75rem;
    margin-right: 2rem;
  }
  &__info {
    max-width: 23.125rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
}
.advantages-audit {
  padding: 6.25rem 0;
  .advantages__inner {
    max-width: 63.4375rem;
  }
  h2 {
    max-width: 50rem;
    color: #0072AF;
  }
  .advantage-card {
    background: rgba(255, 255, 255, 0.8);
    &__title {
      color: #4E5960;
    }
    &__text {
      color: #4E5960;
    }
  }
  .advantages__group {
    display: flex;
    align-items: center;
  }
  .advantages__btn {
    width: 16.25rem;
    margin: 0 1rem 0 0;
  }
  .advantages__info {
    max-width: 27.5rem;
    text-align: start;
    margin: 0;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 9.375rem;
    width: 63.75rem;
  }
}
.advantages-audit2 {
  background-image: url("../img/advantages-bg4.webp") !important;
}


@media screen and (max-width: 1399px) {
  .advantages-audit {
    &__img {
      width: 40rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .services-audit {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .services-audit {
    padding: 4rem 0;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
      margin-top: 2rem;
    }
    &__group {
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .services-audit {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .services-audit {
    &__group {
      flex-direction: column;
      text-align: center;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
  }
  .advantages-audit {
    .advantages__group {
      display: block;
      margin-top: 2rem;
    }
    .advantages__btn {
      margin: 0 0 1rem 0;
    }
  }
}

@media screen and (max-width: 450px) {
  .services-audit {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}