.footer {
  padding: 2.8125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 6.25rem;
  }
  &__left {
    max-width: 15.9375rem;
  }
  &__center {
    flex-grow: 1;
  }
  &__right {
    max-width: 30rem;
    .footer__subtitle {
      margin-bottom: 0.5rem;
    }
  }
  &__logo {
    width: 14rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1rem;
  }
  &__info {
    margin-top: 2.25rem;
    span:nth-child(1) {
      font-size: 1rem;
    }
    span {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      color: #4E5960;
    }
  }
  &__b2b {
    height: 2rem;
    display: inline-flex;
    align-items: center;
    background: #2C2C2C;
    border-radius: 1.25rem;
    padding: 0.1875rem 0.75rem;
    margin-top: 2.25rem;
    span {
      font-size: 0.75rem;
      line-height: 130%;
      color: #FFF;
      margin-right: 0.5rem;
    }
    i {
      flex-shrink: 0;
      width: 3.4375rem;
      img {
        width: 100%;
      }
    }
  }
  &__politic {
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1rem;
    a {
      display: block;
      color: #0072AF;
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2.5rem;
  }
  &__items-left,
  &__items-right {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.25rem;
  }
  &__item {
    max-width: 14.0625rem;
  }
  &__subtitle {
    font-family: 'Tektur';
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 600;
    color: #0072AF;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.5rem;
    margin-top: 0.5rem;
    li {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
    }
    li a:hover {
      color: #87CEF4;
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.25rem;
  }
  &__phone-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__phone-text {
    font-size: 1rem;
    line-height: 130%;
    color: #4E5960;
    margin-right: 0.3125rem;
  }
  &__phone {
    font-family: 'Tektur';
    font-size: 1rem;
    line-height: 130%;
    font-weight: 600;
    color: #333;
    margin-right: 0.3125rem;
  }
  &__btn {
    background: transparent;
    padding: 0;
    span {
      font-size: 1rem;
      line-height: 130%;
      color: #0072AF;
      border-bottom: 0.0625rem dashed #0072AF;
    }
  }
  &__address {
    font-size: 1rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1.25rem;
  }
  &__email-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__email-text {
    font-size: 1rem;
    line-height: 130%;
    color: #4E5960;
    margin-right: 0.3125rem;
  }
  &__email {
    font-size: 1rem;
    line-height: 130%;
    color: #0072AF !important;
    &:hover {
      color: #87CEF4 !important;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-right: 4.6875rem;
  }
  &__social {
    svg path {
      transition: all 0.3s;
    }
    &:hover {
      svg path {
        fill: #87CEF4;
      }
    }
  }
  .yandex {
    width: 13.75rem;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1700px) {
  .footer {
    &__right {
      max-width: 21.875rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__inner {
      gap: 3.125rem;
    }
    &__right {
      max-width: 18.75rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__right {
      max-width: 15.75rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .footer {
    &__inner {
      gap: 2.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    &__inner {
      flex-wrap: wrap;
    }
    &__center {
      flex-grow: unset;
    }
  }
}