.services2 {
  background-image: url("../img/services-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  h2 {
    text-align: center;
    color: #0072AF;
  }
  .services-card {
    background: rgba(255, 255, 255, 0.50);
    &__icon {
      img {
        filter: unset;
      }
    }
    &__title {
      color: #0072AF;
    }
    &__text {
      color: #4E5960;
    }
  }
}
.info3 {
  position: relative;
  background: linear-gradient(180deg, #D4DAE4 0%, #FFF 100%);
  padding: 8.75rem 0 3.75rem;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    gap: 6.25rem;
  }
  &__left {
    max-width: 38.125rem;
    p {
      color: #4E5960;
    }
  }
  h2 {
    color: #0072AF;
  }
  &__items {
    position: relative;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 187.5rem;
      height: 100%;
      background: rgba(255, 255, 254, 0.9);
      border-radius: 1.25rem 0 0 1.25rem;
    }
  }
  &__item {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 2.5rem;
      margin-right: 1rem;
    }
    &-text {
      max-width: 11.5625rem;
      font-size: 1.125rem;
      line-height: 130%;
      color: #4E5960;
    }
  }
  &__body {
    max-width: 88.75rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 3.75rem;
  }
  .stages {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 8.75rem / 3));
    gap: 4.375rem;
  }
  .stage {
    &__img {
      position: relative;
      max-width: 13.75rem;
      padding-bottom: 75%;
      img {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
      }
    }
    &__text {
      position: relative;
      padding-top: 2rem;
      margin-top: 2rem;
      font-size: 1.25rem;
      line-height: 130%;
      color: #4E5960;
      strong {
        font-weight: 700;
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: -0.75rem;
        left: 0;
        width: 1.5rem;
        height: 1.5rem;
        background-image: url("../img/stage-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: calc(100% + 4.375rem);
        height: 0.0625rem;
        border-top: 0.0625rem dashed #4E5960;
      }
    }
  }
  &__group {
    max-width: 16.25rem;
    width: 100%;
    margin-left: 4.375rem;
  }
  &__btn {
    width: 13.25rem;
    height: 3.75rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__btn-text {
    position: relative;
    max-width: 18rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1rem;
    i {
      position: absolute;
      right: -4rem;
      bottom: 0;
      width: 3.625rem;
      img {
        width: 100%;
      }
    }
  }
  &__img {
    position: absolute;
    top: -8.125rem;
    right: 0;

  }
}
.advantages2 {
  background-image: url("../img/advantages-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 3.75rem 0 2rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 9.375rem;
    background: linear-gradient(180deg, #FFF 0%, rgba(255, 255, 255, 0.00) 100%);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5rem;
  }
  &__left {
    width: 35rem;
    margin-right: 3.75rem;
  }
  &__right {
    width: calc(100% - 38.75rem);
  }
  h2 {
    color: #0072AF;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    margin-top: 2rem;
    p {
      font-size: 1.25rem;
      line-height: 130%;
      color: #4E5960;
      margin: 0;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-top: 3.5rem;
  }
  &__btn {
    flex-shrink: 0;
    width: 21.75rem;
    height: 3.75rem;
    margin-right: 1.25rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
}
.staff2 {
  padding: 6.25rem 0;
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 49%;
    max-width: 40rem;
    margin-right: 2.5rem;
  }
  &__right {
    width: 51%;
  }
  h2 {
    color: #0072AF;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5rem;
    margin-top: 2.5rem;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      width: 7rem;
      margin-right: 2.5rem;
    }
    &-desc {
      max-width: 27.1875rem;
    }
    &-title {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #4E5960;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
      margin-top: 0.5rem;
    }
  }
  &__img {
    img {
      width: 100%;
      border-radius: 1.5625rem;
    }
  }
  &__img2 {
    position: absolute;
    top: 0;
    right: 0;
    width: 30rem;
  }
  &__img3 {
    position: absolute;
    top: 0;
    left: 0;
    width: 25rem;
  }
}
.partners2 {
  background: linear-gradient(180deg, rgba(212, 218, 228, 0.00) 0%, #D4DAE4 100%);
  padding: 6.25rem 0;
  &__top {
    max-width: 56.25rem;
    text-align: center;
    margin: 0 auto;
    h2 {
      color: #0072AF;
    }
    p {
      color: #4E5960;
    }
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 4.25rem;
  }
  &__subtitle {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #4E5960;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
    gap: 2rem;
    margin-top: 2rem;
  }
  &__items--active {
    .partners2__item {
      display: flex !important;
    }
  }
  &__item {
    height: 7.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 0.625rem;
    padding: 1rem;
  }
  &__item:nth-child(6) ~ .partners2__item {
    display: none;
  }
  &__link {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 2rem;
    font-family: 'Tektur';
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #0072AF;
    cursor: pointer;
    margin-top: 2rem;
    span:nth-child(2) {
      display: none;
    }
    i {
      flex-shrink: 0;
      display: flex;
      align-items: center;
      margin-left: 0.75rem;
    }
    i svg path {
      fill: currentColor;
    }
    &:hover {
      color: #10a0ea;
    }
  }
  &__link--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
    i {
      transform: rotate(-180deg);
    }
  }
}
.video-block {
  position: relative;
  background: #FFF;
  padding: 6.25rem 0 3.125rem;
  &__inner {
    position: relative;
    z-index: 2;
  }
  h2 {
    text-align: center;
    color: #0072AF;
  }
  .video-wrap {
    max-width: 68.75rem;
    height: 31.25rem !important;
    padding-top: 0 !important;
    margin: 2.5rem auto 0;
  }
  iframe {
    width: 100%;
    height: 100%;
    border-radius: 1.5625rem;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
  }
}
.certificates {
  padding: 3.125rem 0 6.25rem;
  h2 {
    max-width: 55rem;
    text-align: center;
    color: #0072AF;
    margin: 0 auto;
  }
  &__slider {
    margin-top: 2.5rem;
  }
  .slick-list {
    margin: 0 -2rem;
  }
  .slick-slide {
    margin: 0 1rem;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2.5rem;
  }
  &__dots-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin: 0 1rem;
    .slick-active {
      background: #1397DE;
      &:after {
        border-color: #1397DE;
      }
    }
    li {
      position: relative;
      width: 0.75rem;
      height: 0.75rem;
      border-radius: 50%;
      color: transparent;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        border: 0.0625rem solid #7A7B7A;
        border-radius: 50%;
      }
    }
  }
  &__prev {
    position: static;
    width: 2.0625rem;
    height: auto;
    box-shadow: unset;
  }
  &__next {
    position: static;
    width: 2.0625rem;
    height: auto;
    box-shadow: unset;
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: 3.75rem;
    width: 46.25rem;
  }
}


@media screen and (max-width: 1700px) {
  .info3 {
    &__top {
      gap: 2rem;
    }
  }
  .advantages2 {
    &__left {
      width: 31.25rem;
      margin-right: 2.5rem;
    }
    &__right {
      width: calc(100% - 33.75rem);
    }
    &__info {
      margin-top: 2rem;
    }
    &__btn {
      width: 18.75rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .info3 {
    &__left {
      max-width: 100%;
      width: 40%;
      p {
        text-align: start;
      }
    }
    &__right {
      width: 60%;
    }
    .stages {
      grid-template-columns: repeat(3, calc(100% / 3 - 5rem / 3));
      gap: 2.5rem;
    }
    .stage {
      &__text {
        &:before {
          width: calc(100% + 2.5rem);
        }
      }
    }
    &__group {
      margin-left: 2.5rem;
    }
  }
  .advantages2 {
    &__inner {
      padding: 0;
    }
  }
}

@media screen and (max-width: 1399px) {
  .info3 {
    &__body {
      display: block;
    }
    .stage:last-child {
      .stage__text {
        &:before {
          width: calc(100% + 12rem);
        }
      }
    }
    &__group {
      max-width: 13.25rem;
      margin: 2rem 0 0 0;
    }
  }
  .advantages2 {
    &__left {
      width: 25rem;
    }
    &__right {
      width: calc(100% - 27.5rem);
    }
  }
  .staff2 {
    &__items {
      margin-top: 2rem;
      gap: 2rem;
    }
    &__item {
      &-img {
        width: 5rem;
        margin-right: 1.5rem;
      }
      &-title {
        font-size: 1.125rem;
      }
    }
  }
  .partners2 {
    &__item {
      height: 6.25rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .info3 {
    &__items {
      padding: 1.5rem;
    }
    &__item {
      &-icon {
        width: 2rem;
      }
      &-text {
        font-size: 1rem;
      }
    }
  }
  .advantages2 {
    &__left {
      width: 21.875rem;
    }
    &__right {
      width: calc(100% - 24.375rem);
    }
  }
  .partners2 {
    &__body {
      gap: 1.5rem;
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .info3 {
    padding: 2rem 0;
    .stage {
      &__text {
        font-size: 1.125rem;
      }
    }
    &__img {
      top: 0;
    }
  }
  .advantages2 {
    &__left {
      width: 18.75rem;
    }
    &__right {
      width: calc(100% - 21.25rem);
    }
    &__text {
      p {
        font-size: 1rem;
      }
    }
    &__info {
      display: block;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
    &__info-text {
      max-width: 18.75rem;
    }
  }
  .staff2 {
    padding: 4rem 0 2rem;
    &__item {
      &-img {
        width: 2.5rem;
      }
    }
    &__img2 {
      bottom: 0;
    }
  }
  .partners2 {
    padding: 2rem 0 4rem;
    &__body {
      grid-template-columns: 100%;
      margin-top: 2rem;
    }
    &__subtitle {
      font-size: 1.125rem;
    }
    &__items {
      margin-top: 1.5rem;
    }
    &__link {
      font-size: 1rem;
      margin-top: 1.5rem;
    }
  }
  .video-block {
    padding: 4rem 0 2rem 0;
    .video-wrap {
      margin-top: 2rem;
    }
  }
  .certificates {
    padding: 2rem 0 4rem;
    &__slider {
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .info3 {
    &__top {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin-top: 2rem;
    }
    &__items {
      &:after {
        width: 100%;
        border-radius: 1.25rem;
      }
    }
  }
  .advantages2 {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      max-width: 18.75rem;
      margin: 2rem 0 0 0;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &__info {
      display: flex;
    }
    &__btn {
      margin: 0 1rem 0 0;
    }
  }
  .staff2 {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 2rem 0;
    }
    &__right {
      width: 100%;
      max-width: 24rem;
      margin: 0 auto;
    }
  }
  .video-block {
    .fluid-width-video-wrapper {
      height: 25rem !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .info3 {
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.5rem;
    }
    &__item {
      &-text {
        max-width: 100%;
      }
    }
    .stages {
      grid-template-columns: 100%;
    }
    .stage {
      position: relative;
      padding-left: 3.125rem;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 0.0625rem;
        height: calc(100% + 2.5rem);
        border: 0.0625rem dashed #4E5960;
      }
      &__img {
        max-width: 8rem;
        padding-bottom: 0;
        img {
          position: static;
        }
      }
      &__text {
        font-size: 1rem;
        padding: 0;
        margin-top: 2rem;
        &:after {
          top: 0;
          left: -3.875rem;
        }
        &:before {
          display: none;
        }
      }
    }
  }
  .advantages2 {
    &__info {
      display: block;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
  }
}

@media screen and (max-width: 499px) {
  .partners2 {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
  }
}