.services-section {
  background: linear-gradient(180deg, #D4DAE4 0%, rgba(212, 218, 228, 0.00) 100%);
  padding: 5rem 0;
  h2 {
    color: #0072AF;
  }
}
.services2 {
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    position: relative;
    z-index: 1;
    max-width: 43.75rem;
    margin-right: 2.5rem;
  }
  &__right {
    position: relative;
    flex-shrink: 0;
  }
  &__subtitle {
    max-width: 35.3125rem;
    color: #4E5960;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
    gap: 1.5rem;
    margin-top: 5rem;
  }
  &__item {
    &-img {
      max-width: 6.25rem;
      height: 6.25rem;
    }
    &-desc {
      max-width: 11.625rem;
      margin-top: 1rem;
    }
    &-title {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #4E5960;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
      margin-top: 0.5rem;
    }
  }
  &-card {
    max-width: 27.5rem;
    background: #ffffff;
    backdrop-filter: blur(0.625rem);
    border-radius: 1.25rem;
    padding: 3.125rem;
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 1rem;
      li {
        position: relative;
        font-size: 1.25rem;
        line-height: 130%;
        color: #4E5960;
        padding-left: 2.375rem;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 1.5rem;
          height: 1.5rem;
          background-image: url("../img/check2.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
      }
    }
  }
  &__btn {
    width: 14.375rem;
    height: 3.75rem;
    margin-top: 2.5rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__info {
    position: relative;
    max-width: 16.5625rem;
    text-align: center;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 1.5rem;
    i {
      position: absolute;
      right: -4rem;
      bottom: 1rem;
      width: 3.625rem;
      img {
        width: 100%;
      }
    }
  }
  &__img {
    position: absolute;
    right: 6.25rem;
    bottom: -3.75rem;
    width: 43.75rem;
  }
}
.professionals {
  padding: 5rem 0 7.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: 48%;
    margin-right: 6.25rem;
  }
  &__right {
    width: 52%;
  }
  h2 {
    color: #0072AF;
  }
  &__subtitle {
    color: #4E5960;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.125rem / 2));
    gap: 3.125rem;
    margin-top: 2.5rem;
  }
  &__item {
    &-img {
      max-width: 7.1875rem;
      height: 5.625rem;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
      margin-top: 1rem;
      strong {
        font-weight: 700;
      }
    }
  }
  &__group {
    display: flex;
    margin-top: 2.5rem;
  }
  &__btn {
    width: 60%;
    height: 3.75rem;
    margin-right: 1rem;
  }
  &__info {
    width: 40%;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
  .cloud-img {
    position: absolute;
    left: 0;
    bottom: -15rem;
    width: 43.75rem;
  }
  .cloud-img2 {
    position: absolute;
    left: 50%;
    bottom: -15rem;
    transform: translate(-50%, 0);
    width: 40.625rem;
  }
  .cloud-img3 {
    position: absolute;
    right: 0;
    bottom: -15rem;
    width: 47.5rem;
  }
}
.section-group3 {
  background-image: url("../img/section-group__bg3.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 6.25rem;
}
.services3 {
  padding: 12.5rem 0 6.25rem;
  &__inner {
    max-width: 45.75rem;
    margin-left: auto;
  }
  h2 {
    color: #FFDD00;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 2.5rem;
  }
  &__group {
    display: flex;
    align-items: center;
    margin-top: 3.75rem;
  }
  &__btn {
    flex-shrink: 0;
    width: 15.625rem;
    height: 3.75rem;
    margin-right: 2rem;
  }
  &__info {
    max-width: 28.125rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #FD0;
  }
}
.factors {
  background: linear-gradient(180deg, #D4DAE4 0%, rgba(212, 218, 228, 0.00) 100%);
  padding: 5rem 0;
  &__inner {
    max-width: 100%;
  }
  h2 {
    color: #0072AF;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: 50%;
    margin-right: 2.5rem;
  }
  &__right {
    width: 50%;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 2.5rem;
  }
  &__img {
    max-width: 45rem;
    margin-left: auto;
  }
  .advantage-card {
    background: #FFF;
    &__title {
      color: #0072AF;
    }
    &__text {
      color: #4E5960;
    }
  }
}


@media screen and (max-width: 1500px) {
  .factors {
    &__inner {
      position: unset;
    }
    &__wrap {
      display: block;
    }
    &__left {
      position: relative;
      z-index: 1;
      width: 100%;
      max-width: 45.75rem;
    }
    &__right {
      width: 100%;
    }
    &__img {
      position: absolute;
      top: 50%;
      right: 1rem;
      transform: translate(0, -50%);
      max-width: 32rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .services2 {
    &__item {
      &-img {
        width: 4rem;
        height: 4rem;
      }

    }
  }
  .professionals {
    &__inner {
      flex-direction: column;
    }
    &__left {
      max-width: 25rem;
      width: 100%;
      margin: 2rem auto 0;
    }
    &__right {
      order: -1;
      width: 100%;
    }
    &__group {
      max-width: 37.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .services-section {
    padding: 2rem 0;
  }
  .services2 {
    &__inner {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin: 0 0 4rem 0;
    }
    &-card {
      padding: 2rem;
      margin-left: auto;
    }
    &__items {
      margin-top: 2.5rem;
    }
    &__item {
      &-title {
        font-size: 1.25rem;
      }
    }
  }
  .professionals {
    padding: 2rem 0;
    .cloud-img,
    .cloud-img2,
    .cloud-img3 {
      bottom: -6.25rem;
    }
  }
  .section-group3 {
    padding-top: 0;
  }
  .services3 {
    padding: 2rem 0 4rem;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      gap: 1.5rem;
      margin-top: 2rem;
    }
    &__group {
      margin-top: 2rem;
    }
  }
  .factors {
    padding: 4rem 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      gap: 1.5rem;
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .services2 {
    &__left {
      margin-bottom: 2rem;
    }
    &__img {
      width: 32rem;
    }
  }
}

@media screen and (max-width: 576px) {
  .services2 {
    &__items {
      grid-template-columns: 100%;
      gap: 1.5rem;
    }
    &__item {
      display: flex;
      &-img {
        width: 3.125rem;
        height: auto;
        flex-shrink: 0;
      }
      &-desc {
        max-width: 100%;
        margin: 0 0 0 1.5rem;
      }
    }
  }
  .professionals {
    &__items {
      grid-template-columns: 100%;
      gap: 2rem;
    }
    &__item {
      display: flex;
      &-img {
        flex-shrink: 0;
        width: 3.125rem;
        height: auto;
      }
      &-text {
        margin: 0 0 0 1.5rem;
      }
    }
    &__group {
      display: block;
    }
    &__btn {
      width: 100%;
      max-width: 28.75rem;
      margin: 0 0 1rem 0;
    }
    &__info {
      width: 100%;
    }
  }
  .services3 {
    &__group {
      display: block;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
  }
  .factors {
    &__img {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .services3 {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .factors {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}