h2 {
  font-size: 2.25rem;
  line-height: 130%;
  font-weight: 600;
  margin: 0;
}
.btn {
  padding: 0.3125rem;
  span {
    min-width: auto;
    padding: 0;
  }
  &:focus,
  &:visited,
  &:active {
    display: flex;
  }
}
.btn-blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1397DE;
  border-radius: 1rem;
  span {
    font-family: 'Tektur';
    line-height: 130%;
    font-weight: 600;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    color: #FFF;
  }
  &:hover {
    background: #87CEF4;
    span {
      color: #4E5960;
    }
  }
  &:active {
    background: #4E5960;
    span {
      color: #ffffff;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 3rem;
  height: 1.5rem;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    height: 0.1875rem;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
    width: 2.5rem;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
    width: 100%;
  }
  span:nth-child(3) {
    bottom: 0;
    width: 2.5rem;
  }
  &--active {
    span:nth-child(1) {
      top: 0.625rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.65625rem;
      transform: rotate(45deg);
    }
  }
}
.sidebar {
  position: fixed;
  z-index: 7;
  top: 0;
  left: 0;
  width: 8.75rem;
  height: 100vh;
  background: #1397DE;
  padding: 1.875rem 1rem 5rem;
  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .menu2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
  }
  .burger {
    span {
      background: #ffffff;
    }
  }
  .menu-text {
    font-family: 'Tektur';
    font-size: 1rem;
    line-height: 105%;
    color: rgba(255, 255, 255, 0.25);
    margin-top: 0.5rem;
  }
  &__text {
    flex-shrink: 0;
    font-size: 4rem;
    line-height: 120%;
    color: rgba(255, 255, 255, 0.25);
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    margin-bottom: 2rem;
  }
  &__menu {
    position: fixed;
    z-index: 6;
    top: 0;
    left: -105%;
    width: 55%;
    height: 100vh;
    background: #D4DAE4;
    padding: 1.625rem 3rem 3.125rem 12.5rem;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
  }
  &__logo {
    display: block;
    width: 14.0625rem;
    img {
      width: 100%;
    }
  }
  &__nav {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 8.75rem;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
  }
  &__btn {
    width: 14.375rem;
    height: 2.9375rem;
    margin-top: 3.75rem;
    span {
      font-size: 1rem;
    }
  }
}
.sidebar__menu {
  .li {
    font-family: 'Tektur';
    font-size: 1.5rem;
    line-height: 130%;
    color: #0072AF;
    cursor: pointer;
    &:hover {
      color: #87CEF4;
    }
    &--active {
      color: #87CEF4;
    }
  }
  .li__content {
    display: none;
    font-family: 'Tektur';
    font-size: 1.25rem;
    line-height: 130%;
    color: #0072AF;
    a {
      display: block;
    }
    a:not(:first-child) {
      margin-top: 0.625rem;
    }
    a:hover {
      color: #87CEF4;
    }
  }
  .li__content--active {
    display: block;
  }
}
.search {
  position: fixed;
  z-index: 6;
  top: 0;
  left: -105%;
  width: 100%;
  height: 100vh;
  background: #D4DAE4;
  padding: 14.0625rem 3rem 3.125rem 12.5rem;
  transition: all 0.5s;
  overflow-y: auto;
  &--active {
    left: 0;
  }
  &-btn {
    display: inline-flex;
    color: #0072AF;
    cursor: pointer;
    margin-top: 3.75rem;
    &:hover {
      color: #87CEF4;
    }
    span {
      font-family: 'Tektur';
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 600;
    }
    i {
      flex-shrink: 0;
      width: 2.25rem;
      margin-left: 0.625rem;
    }
    i svg {
      width: 100%;
      height: auto;
      path {
        fill: currentColor;
      }
    }
  }
  h2 {
    color: #0072AF;
  }
  &__input {
    display: flex;
    align-items: center;
    margin-top: 2.5rem;
    input {
      max-width: 30rem;
      width: 100%;
      height: 2.8125rem;
      background-image: url("../img/search-icon.svg");
      background-repeat: no-repeat;
      background-position: left 1rem top 50%;
      background-size: 1.5rem;
      background-color: #FFF;
      border-color: transparent;
      border-radius: 0.625rem;
      padding: 0 1rem 0 3.125rem;
      font-size: 1.125rem;
      line-height: 130%;
      color: #4E5960;
      &::placeholder {
        color: #D4DAE4;
      }
    }
    button {
      flex-shrink: 0;
      width: 7.5rem;
      height: 2.8125rem;
      margin-left: 1.5rem;
      span {
        font-size: 1rem;
      }
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #4E5960;
    margin-top: 3.75rem;
  }
}
.sidebar-form {
  position: fixed;
  z-index: 6;
  top: 0;
  left: -105%;
  width: 100%;
  max-width: 100%;
  height: 100vh;
  background: #D4DAE4;
  border-radius: 0;
  padding: 1.625rem 3rem 3.125rem 12.5rem;
  transition: all 0.5s;
  overflow-y: auto;
  &--active {
    left: 0;
  }
  &__block {
    display: flex;
    margin-top: 8.75rem;
  }
  &__left {
    max-width: 21.875rem;
    margin-right: 2rem;
  }
  &__right {
    max-width: 45.9375rem;
    width: 100%;
  }
  h3 {
    font-size: 1.5rem;
    line-height: 130%;
    color: #0072AF;
    margin: 0;
  }
  &__link {
    width: 11.875rem;
    height: 2.8125rem;
    margin-top: 3.75rem;
  }
  &__politics {
    display: block;
    font-size: 0.875rem;
    line-height: 130%;
    color: #0072AF;
    margin-top: 3.75rem;
    &:hover {
      color: #87CEF4;
    }
  }
  h2 {
    color: #0072AF;
    margin-bottom: 2.5rem;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 1.5rem 2rem;
  }
  .form-group--100 {
    grid-column: 1/3;
  }
  .box {
    display: flex;
    align-items: center;
    input {
      height: auto;
    }
    .inputfile:focus ~ label {
      outline: none;
    }
    label i {
      width: 1.5rem;
      margin-right: 1rem;
    }
    label svg {
      width: 100%;
      height: auto;
    }
    label span {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
    }
  }
  &__btn {
    width: 14.375rem;
    height: 2.8125rem;
    margin-left: auto;
  }
  .agreement-check {
    grid-column: 1/3;
  }
  .agreement-label {
    max-width: 100% !important;
  }
}
.page-top {
  z-index: 3;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 20rem 0 16.875rem 0;
  }
  &__desc {
    position: relative;
    z-index: 1;
    max-width: 47.4375rem;
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    font-weight: 600;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: #0072AF;
    margin: 0;
    span {
      font-size: 5rem;
      line-height: 130%;
    }
    .text-color {
      font-size: 3rem;
      color: #4E5960;
    }
  }
  p {
    max-width: 40.625rem;
    font-size: 1.75rem;
    line-height: 140%;
    color: #4E5960;
    margin: 2.5rem 0 0 0;
    strong {
      font-weight: 700;
    }
  }
  &__info {
    display: flex;
    align-items: stretch;
    background: #FFF;
    border-radius: 0.9375rem;
    margin-top: 7.1875rem;
  }
  &__info2 {
    max-width: 43.75rem;
    .page-top__btn {
      width: 40%;
    }
    .page-top__info-text {
      width: 60%;
      padding: 0.3125rem 1.5rem;
    }
  }
  &__info3 {
    .page-top__btn {
      width: 50%;
    }
    .page-top__info-text {
      width: 50%;
      padding: 0.3125rem 1.5rem;
    }
  }
  &__btn {
    width: 55%;
    height: 4.8125rem;
  }
  &__info-text {
    width: 45%;
    height: 4.8125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3125rem 2.5rem;
    font-size: 1rem;
    line-height: 130%;
    color: #424647;
    strong {
      display: contents;
      font-weight: 700;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50rem;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
    &:hover {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  &__img2 {
    position: absolute;
    top: 10rem;
    right: -8.125rem;
    width: 19.375rem;
  }
  &__img3 {
    position: absolute;
    z-index: 2;
    right: 3.75rem;
    bottom: 6.5625rem;
  }
  &__bottom {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    background: rgba(255, 255, 255, 0.70);
    backdrop-filter: blur(0.9375rem);
    padding: 0.9375rem 0;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      max-width: 8.5rem;
      margin-right: 1.875rem;
    }
    &-text {
      max-width: 16.5625rem;
      font-size: 1.25rem;
      line-height: 130%;
      color: #4E5960;
      strong {
        font-weight: 700;
      }
    }
  }
  &__img4 {
    position: absolute;
    right: 5rem;
    bottom: -4rem;
    width: 36.25rem;
  }
  &__img5 {
    position: absolute;
    right: 0;
    bottom: 6.25rem;
    width: 16.25rem;
    animation: img 8s infinite linear;
  }
  &__img6 {
    position: absolute;
    top: 15.3125rem;
    left: 53%;
    transform: translate(-50%, 0);
    width: 10.9375rem;
  }
  .cloud-img {
    position: absolute;
    left: 0;
    bottom: -6.25rem;
    width: 31.25rem;
  }
  .cloud-img2 {
    position: absolute;
    left: 40%;
    bottom: -8.75rem;
    transform: translate(-50%, 0);
    width: 40rem;
  }
  &__img7 {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 47rem;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
    &:hover {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  &__img8 {
    position: absolute;
    top: 10rem;
    right: 11.875rem;
    width: 11.875rem;
    animation: img 8s infinite linear;
  }
  &__img9 {
    position: absolute;
    left: 22%;
    bottom: -4rem;
    width: 15rem;
  }
  &__img10 {
    position: absolute;
    right: 6.25rem;
    bottom: 4rem;
    width: 48.125rem;
  }
  &__img11 {
    position: absolute;
    top: 7.5rem;
    right: 0;
    width: 15.4375rem;
  }
  &__img12 {
    position: absolute;
    top: 12.5rem;
    right: 30%;
    transform: translate(-50%, 0);
    width: 12.5rem;
    animation: img 8s infinite linear;
  }
  &__img13 {
    position: absolute;
    right: -2.5rem;
    bottom: 0;
    width: 71.875rem;
  }
}
.subtitle {
  font-size: 1.5rem;
  line-height: 130%;
  margin: 2rem 0 0 0;
}
.form-field {
  background: transparent;
  border: none;
  margin: 0;
  input {
    width: 100%;
    height: 3.125rem;
    background: #FFF;
    border: none;
    border-radius: 0.625rem;
    padding: 0.1875rem 1.875rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #000000;
    &::placeholder {
      color: #666;
    }
  }
  textarea {
    width: 100%;
    height: 7.8125rem;
    background: #FFF;
    border: none;
    border-radius: 0.625rem;
    padding: 1rem 1.875rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #000000;
    resize: none;
    &::placeholder {
      color: #666;
    }
  }
}
.close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 1.5rem;
  cursor: pointer;
  svg {
    width: 100%;
    height: auto;
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes img {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translate(-15%, 0);
  }
  100% {
    transform: translate(0);
  }
}
.top-animation {
  transform: translate(0, -60%);
  opacity: 0;
  transition: all 0.8s;
}
.top-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.bottom-animation {
  transform: translate(0, 60%);
  opacity: 0;
  transition: all 0.8s;
}
.bottom-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.left-animation {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.8s;
}
.left-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-animation {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.8s;
}
.right-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  display: grid;
  grid-template-columns: 100%;
  gap: 1.5rem;
  padding: 3.125rem 0;
  a {
    font-size: 1.5rem;
    line-height: 130%;
    color: #0072AF;
    &:hover {
      color: #87CEF4;
    }
  }
}


