.mistake {
  background-image: url("../img/mistake-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 16rem 0 5rem;
  h1 {
    font-family: 'RobotoMono';
    font-size: 17.5rem;
    line-height: 105%;
    color: #1397DE;
    margin: 0;
  }
  h2 {
    color: #FFF;
    margin-top: 2rem;
  }
  &__text {
    font-size: 1.5rem;
    line-height: 130%;
    color: #FFF;
    margin-top: 2rem;
  }
  &__btn {
    max-width: 21.625rem;
    height: 4.8125rem;
    margin-top: 3.75rem;
    span {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .mistake {
    padding: 15.625rem 0 9.375rem;
    h1 {
      font-size: 12.5rem;
    }
  }
}

@media screen and (max-width: 1250px) {
  .mistake {
    padding-top: 12rem;
  }
}

@media screen and (max-width: 1023px) {
  .mistake {
    padding: 9.375rem 0 5rem;
    h1 {
      font-size: 9.375rem;
    }
    &__text {
      margin-right: 1rem;
    }
    &__btn {
      height: 3.75rem;
      margin-top: 2rem;
    }
  }
}