.section-wrap {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% - 50rem);
    background-image: url("../img/section-wrap__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}
.section-group {
  position: relative;
  z-index: 2;
  background-image: url("../img/section-group__bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: -3.75rem;
}
.info {
  background: radial-gradient(19.46% 50% at 50% 50%, rgba(227, 232, 236, 0.00) 0%, #D3DAE3 100%);
  padding: 10rem 0 3.75rem;
  .diagram {
    position: relative;
    max-width: 35.625rem;
    filter: drop-shadow(0 1.875rem 3.125rem rgba(78, 89, 96, 0.35));
    margin: 0 auto;
  }
  .diagram__circle {
    animation: spin 8s infinite linear;
  }
  #showrndimages {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 21.875rem;
  }
  .diagram__item {
    position: absolute;
    max-width: 21.25rem;
    display: flex;
    border-bottom: 0.0625rem solid #7A7B7A;
    padding-bottom: 1.5rem;
    &-desc {
      position: relative;
      max-width: 13rem;
      strong {
        font-family: 'Tektur';
        font-size: 2rem;
        line-height: 130%;
        font-weight: 600;
        color: #4E5960;
      }
      p {
        font-size: 1rem;
        line-height: 130%;
        color: #4E5960;
        margin: 0.5rem 0 0 0;
      }
    }
    &-img {
      flex-shrink: 0;
      max-width: 7.8125rem;
    }
  }
  .diagram__item:nth-child(1) {
    top: 0;
    left: -21.25rem;
  }
  .diagram__item:nth-child(2) {
    top: 50%;
    left: -26.25rem;
    transform: translate(0, -50%);
    &:before {
      right: -8.125rem;
      width: 6.25rem;
    }
  }
  .diagram__item:nth-child(3) {
    left: -21.25rem;
    bottom: -1rem;
    &:before {
      right: -8rem;
      width: 6.25rem;
    }
  }
  .diagram__item:nth-child(4) {
    top: 0;
    right: -21.25rem;
  }
  .diagram__item:nth-child(5) {
    right: -24.25rem;
    bottom: 7.8125rem;
    &:before {
      left: -5.6rem;
      width: 3.75rem;
    }
  }
  .left-item {
    &:after {
      content: '';
      position: absolute;
      right: -1rem;
      bottom: -0.4375rem;
      transform: rotate(45deg);
      width: 0.0625rem;
      height: 2.8125rem;
      background: #7A7B7A;
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 1.9rem;
      right: -3.125rem;
      width: 1.25rem;
      height: 0.0625rem;
      background: #7A7B7A;
    }
    .diagram__item-desc {
      text-align: right;
      margin-right: 2rem;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1.5rem;
        width: 3.5rem;
        height: 0.1875rem;
        background: #7A7B7A;
      }
    }
  }
  .right-item {
    &:after {
      content: '';
      position: absolute;
      left: -1rem;
      bottom: -0.4375rem;
      transform: rotate(-45deg);
      width: 0.0625rem;
      height: 2.8125rem;
      background: #7A7B7A;
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 1.9rem;
      left: -3.125rem;
      width: 1.25rem;
      height: 0.0625rem;
      background: #7A7B7A;
    }
    .diagram__item-desc {
      order: 1;
      text-align: left;
      margin-left: 2rem;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: -1.5rem;
        width: 3.5rem;
        height: 0.1875rem;
        background: #7A7B7A;
      }
    }
  }
}
