.calculator {
  padding: 3.75rem 0;
  &__inner {
    position: relative;
    max-width: 71.875rem;
    background: #D4DAE4;
    border-radius: 1.25rem;
    padding: 3.75rem;
    margin: 0 auto;
  }
  h2 {
    text-align: center;
    color: #0072AF;
  }
  &__wrap {
    align-items: flex-end;
    margin-top: 1.875rem;
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: flex;
  }
  &__left {
    width: 50%;
    margin-right: 4rem;
  }
  &__right {
    width: 50%;
  }
  &__checkboxes {
    margin-top: 2.5rem;
    &-title {
      font-size: 1rem;
      line-height: 130%;
      color: #4E5960;
    }
    &-wrap {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 1.5rem;
      margin-top: 1rem;
    }
  }
  &__checkbox {
    position: relative;
    display: flex;
    align-items: center;
    label {
      position: relative;
      font-size: 1.125rem;
      line-height: 130%;
      color: #4E5960;
      padding-left: 2rem;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.5rem;
        height: 1.5rem;
        background: #ffffff;
        border: 0.0625rem solid #1397DE;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0.375rem;
        left: 0.375rem;
        width: 0.75rem;
        height: 0.75rem;
        background: #1397DE;
        border-radius: 50%;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  .tab--active {
    label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
  &__ranges {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.75rem;
  }
  &__range {
    &-title {
      font-family: 'Tektur';
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      color: #0072AF;
    }
    ._range-block {
      position: relative;
      padding-right: 7.125rem;
      margin-top: 2.8125rem;
    }
  }
  .range-input {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 4rem;
    height: 2.875rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #FFF;
    border-color: transparent;
    border-radius: 0.1875rem;
    font-family: 'Tektur';
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 600;
  }
  .range-input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }
  .noUi-pips {
    top: -2rem;
    height: 2rem;
    padding: 0;
  }
  .noUi-marker {
    bottom: 0.3125rem;
    display: none;
  }
  .noUi-marker-large {
    width: 0.0625rem;
    height: 0.5rem;
    display: block;
  }
  .noUi-value {
    top: -0.5rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #828282;
  }
  .noUi-target {
    height: 0.75rem;
    background: #FFF;
    border: none;
    box-shadow: unset;
    border-radius: 0.1875rem;
  }
  .noUi-connect {
    background: #4E5960;
  }
  .noUi-handle {
    top: -0.25rem;
    right: -0.9375rem;
    width: 1.25rem;
    height: 1.25rem;
    background: #0072AF;
    border: none;
    box-shadow: unset;
    border-radius: 50%;
    &:before,
    &:after {
      display: none;
    }
  }
  .range-slider4 {
    .noUi-value {
      &:after {
        content: 'мес';
        display: inline-block;
      }
    }
  }
  .range-slider6 {
    .noUi-value {
      &:after {
        content: 'млн';
        display: inline-block;
      }
    }
  }
  &__images {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    .range-number {
      position: absolute;
      top: -4.25rem;
      left: 50%;
      transform: translate(-50%, 0);
      width: 4.25rem;
      height: 4.25rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      font-family: 'Tektur';
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -2.1875rem;
        transform: translate(-50%, 0);
        border: 1.25rem solid transparent;
        border-top: 1.875rem solid #ffffff;
        pointer-events: none;
      }
    }
  }
  &__img {
    position: relative;
    width: 11.125rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 15rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: relative;
    width: 12.5rem;
    img {
      width: 100%;
    }
    .range-number2-1 {
      top: 0;
      left: unset;
      right: 2.5rem;
      transform: unset;
    }
  }
  &__price {
    margin-top: 2.5rem;
    &-title {
      font-family: 'Tektur';
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      color: #4E5960;
    }
    &-number {
      font-family: 'Tektur';
      font-size: 1.5rem;
      line-height: 130%;
