.services4 {
  z-index: 1;
  background-image: url("../img/services-bg2.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 12.5rem 0 7.8125rem 0;
  margin-top: -4rem;
  h2 {
    max-width: 38.25rem;
    color: #0072AF;
    margin-left: auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 6rem / 4));
    gap: 2rem;
    margin-top: 3.125rem;
  }
  .services-card {
    background: rgba(255, 255, 255, 0.50);
    &__icon {
      img {
        filter: unset;
      }
    }
    &__title {
      color: #0072AF;
    }
    &__text {
      color: #4E5960;
    }
  }
  .cloud__img {
    position: absolute;
    top: -6.25rem;
    left: 0;
    width: 42.8125rem;
  }
  .cloud__img2 {
    position: absolute;
    top: -3.125rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 53.125rem;
  }
  .cloud__img3 {
    position: absolute;
    top: -3.125rem;
    right: 0;
    width: 31.75rem;
  }
}
.advice {
  background-image: url("../img/advice-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 14.0625rem 0 7.8125rem 0;
  margin-top: -6.25rem;
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 81.875rem;
    background: rgba(0, 0, 0, 0.70);
    backdrop-filter: blur(0.625rem);
    border-radius: 0.9375rem;
    padding: 3.125rem 3.75rem;
    margin: 0 auto;
  }
  h2 {
    max-width: 50rem;
    text-align: center;
    color: #FD0;
    margin: 0 auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 6rem / 4));
    gap: 2rem;
    margin-top: 3.75rem;
  }
  .advantage-card {
    background: transparent;
    padding: 0;
  }
  &__group {
    max-width: 48.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 0.9375rem;
    margin: 3.5rem auto 0;
  }
  &__btn {
    flex-shrink: 0;
    width: 15.3125rem;
    height: 3.75rem;
  }
  &__info {
    height: 3.75rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.1875rem 1rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
}
.partners3 {
  padding: 37.5rem 0;
  margin-top: -37.5rem;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/partners-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  h2 {
    max-width: 50rem;
    text-align: center;
    color: #FD0;
    margin: 0 auto;
  }
  .partners2__body {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
  }
  .partners2__subtitle {
    text-align: center;
    color: #FFF;
  }
  .partners2__items {
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
  }
  &__group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3.125rem;
  }
  &__btn {
    flex-shrink: 0;
    width: 13.5rem;
    height: 3.75rem;
    margin-right: 2rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__info {
    max-width: 13.5rem;
    font-size: 0.875rem;
    line-height: 130%;
    color: #4E5960;
  }
}
.offer2 {
  z-index: 2;
  background-image: url("../img/offer-bg2.webp");
  margin-top: -31.25rem;
}
.advantages4 {
  padding: 18.625rem 0 6.25rem;
  margin-top: -9.25rem;
  .advantages__inner {
    max-width: 63.4375rem;
  }
  h2 {
    max-width: 50rem;
    color: #0072AF;
  }
  .advantage-card {
    text-align: start;
    background: rgba(255, 255, 255, 0.70);
    backdrop-filter: blur(0.5rem);
    &__icon {
      max-width: 8.125rem;
      width: auto;
      height: 6.75rem;
    }
    &__text {
      color: #4E5960;
    }
  }
  .advantages__group {
    display: flex;
    align-items: center;
  }
  .advantages__btn {
    margin: 0 1rem 0 0;
  }
  .advantages__info {
    text-align: start;
    margin: 0;
  }
}

@media screen and (max-width: 1399px) {
  .services4 {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
    }
  }
  .advice {
    margin-top: -9rem;
  }
  .partners3 {
    .partners2__subtitle {
      font-size: 1.25rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .partners3 {
    padding-top: 50rem;
    margin-top: -50rem;
    .partners2__body {
      grid-template-columns: 100%;
      gap: 1.5rem;
    }
    .partners2__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .services4 {
    padding: 8rem 0 4rem;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
      margin-top: 2rem;
    }
    .cloud__img {
      top: -3.125rem;
      width: 26rem;
    }
    .cloud__img2 {
      top: -2rem;
      width: 40rem;
    }
    .cloud__img3 {
      top: -2rem;
      width: 20rem;
    }
  }
  .advice {
    padding: 10rem 0 4rem;
    &__inner {
      padding: 2.5rem 2rem;
    }
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 4.5rem / 4));
      gap: 1.5rem;
      margin-top: 2rem;
    }
  }
  .partners3 {
    padding-bottom: 16rem;
    .partners2__subtitle {
      font-size: 1.125rem;
    }
    &__group {
      margin-top: 2rem;
    }
  }
  .offer2 {
    padding: 4rem 0;
    margin-top: -16rem;
  }
  .advantages4 {
    padding: 11rem 0 4rem;
  }
}

@media screen and (max-width: 768px) {
  .services4 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
  }
  .advice {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
    &__group {
      flex-direction: column;
      text-align: center;
      background: transparent;
    }
    &__info {
      color: #ffffff;
      padding: 0;
      margin: 1rem 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages4 {
    .advantages__group {
      display: block;
    }
    .advantages__btn {
      margin: 0 0 1rem 0;
    }
  }
}

@media screen and (max-width: 499px) {
  .partners3 {
    .partners2__items {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
    &__group {
      flex-direction: column;
    }
    &__btn {
      margin: 0 0 1rem 0;
    }
    &__info {
      text-align: center;
    }
  }
}

@media screen and (max-width: 450px) {
  .services4 {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .advice {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}