.gratitude {
  background-image: url("../img/gratitude-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 22rem 0 12.5rem;
  &__inner {
    position: relative;
    max-width: 53.125rem;
    padding: 6rem 6rem 6rem 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 187.5rem;
      height: 100%;
      background: rgba(255, 255, 255, 0.60);
      backdrop-filter: blur(0.5rem);
      border-radius: 1.5625rem;
    }
  }
  &__desc {
    position: relative;
    z-index: 1;
    p {
      max-width: 31.875rem;
      font-size: 1.5rem;
      line-height: 130%;
      color: #4E5960;
      margin: 2rem 0 0 0;
    }
  }
  h1 {
    font-size: 2.25rem;
    line-height: 130%;
    font-weight: 600;
    color: #0072AF;
    margin: 0;
  }
  &__btn {
    max-width: 21.625rem;
    height: 4.8125rem;
    margin-top: 2rem;
    span {
      font-size: 1.125rem;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: -15rem;
    bottom: -12.5rem;
    width: 25rem;
    p {
      position: absolute;
      right: -10rem;
      bottom: 5rem;
      font-family: 'Tektur';
      font-size: 5rem;
      line-height: 130%;
      font-weight: 600;
      color: #D4DAE4;
    }
  }
}

@media screen and (max-width: 1399px) {
  .gratitude {
    &__img {
      right: -8rem;
      p {
        right: 0;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .gratitude {
    padding-top: 20rem;
  }
}

@media screen and (max-width: 1023px) {
  .gratitude {
    padding: 12rem 0 6.25rem;
    &__desc {
      p {
        font-size: 1rem;
        margin-top: 1rem;
      }
    }
    h2 {
      font-size: 1.75rem;
    }
    &__btn {
      height: 3.75rem;
    }
    &__img {
      right: -2rem;
      bottom: -6.25rem;
      width: 18rem;
      p {
        bottom: 3rem;
        font-size: 2rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .gratitude {
    &__inner {
      max-width: 36rem;
      padding-right: 2.5rem;
    }
    &__img {
      display: none;
    }
  }
}